export const required = value => {
    if (!value) return `  `;
    return undefined;    
}

//  Thunk   ,         .
export const maxLengthCreator = (maxLength) => (value) => {
    if (value.length > maxLength) return `  ${maxLength} `;
    return undefined;
}

//        ,      ,    .
const maxLength30 = value => {
    if (value && value.length > 30) return `Max length is 30 symbols`;
    return undefined;    
}

